* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-image: url("../public/img/bg.png");
  background-repeat: no-repeat;
  font-family: "Roboto", sans-serif;
}
.container {
  width: 1590px;
  max-width: 100%;
  margin: 0 auto;
}
img {
  max-width: 100%;
}
.section__inner {
  display: flex;
  justify-content: space-between;
  padding-top: 200px;
}
.section__title {
  width: 40%;
}
.section__title h1 {
  font-weight: 300;
  font-size: 96px;
  font-weight: 300;
  line-height: 112px;
  text-transform: uppercase;
}
/**КАЛЬКУЛЯТОР**/
.calculate {
  height: 565px;
  width: 350px;
  background-color: #6ec4ff;
  padding: 90px 35px 35px 35px;
  border: 1px solid #6ec4ff;
  border-radius: 10px;
}
.result__inner {
  text-align: right;
  padding-bottom: 50px;
}
.result__prev {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: rgba(0, 0, 0, 0.47);
}
.result {
  font-weight: 500;
  font-size: 40px;
  line-height: 47px;
  color: #000000;
}
.buttons {
  display: flex;
  align-items: center;

  flex-wrap: wrap;
}
.button {
  margin: 6px;
  height: 55px;
  width: 55px;
  text-align: center;
  line-height: 55px;
  border: 1px solid rgba(255, 255, 255, 0.46);
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.46);
  font-weight: 700;
  font-size: 24px;
  cursor: pointer;
  color: #ffffff;
  user-select: none;
  transition: filter 0.3s ease-in-out;
}
.leng {
  width: 120px;
}

.button:hover {
  filter: brightness(50%);
}
.button:active {
  filter: brightness(80%);
}
.blue {
  background-color: rgba(79, 160, 255, 0.45);
}
